<template>
    <div fluid style="height: 100%;">
        <VueDragResize v-for="(model,index) in models" :key="index" :class="'itemModel'+index" w="auto" h="auto" :x="model.position.x" :y="model.position.y" :z="model.position.z" :isDraggable="true"
            :isResizable="false" :parentLimitation="true">
            <v-card>
                <v-container>
                    <v-img lazy-src="https://picsum.photos/id/11/10/6" max-width="300" :src="model.src"></v-img>
                    <v-card-actions :class="model.sideIcons">
                        <div v-if="model.icons.length!=1">
                            <v-btn icon v-for="iconShow in model.icons" :key="iconShow">
                                <v-icon>
                                    {{iconShow}}
                                </v-icon>
                            </v-btn>
                        </div>
                        <div v-else>
                            <v-btn icon v-for="i in 3" :key="i">
                                <v-icon>
                                    {{model.icons[0]}}
                                </v-icon>
                            </v-btn>
                        </div>
                    </v-card-actions>
                </v-container>
            </v-card>
        </VueDragResize>
        <p class="text-center" style="position:absolute;top:50%;width: 100%;">
            Mira mas de mis trabajos!!
        </p>
    </div>
</template>

<style>
    .filler {
        width: 100%;
        height: 100%;
        display: inline-block;
        position: absolute;
    }
    .growHoover{
        transition: all .2s ease-in-out;
        transform: scale(1.1);
        z-index: 99!important;
    }
    .vdr.active:before{
        outline: 0px dashed #d6d6d6!important;
    }
</style>

<script>
    import VueDragResize from 'vue-drag-resize';

    export default {
        components: {
            VueDragResize,
        },

        data(){
            return {
                models: [{
                    id: 1,
                    src: require('../assets/modelOne.jpg'),
                    icons: ["fas fa-fire"],
                    position: {
                        y: 56,
                        x: 150,
                        z:5
                    },
                    sideIcons:''
                },
                {
                    id: 2,
                    src: require('../assets/modelTwo.jpg'),
                    icons: ["fas fa-heart", "far fa-heart"],
                    position: {
                        y: 66,
                        x: 665,
                        z:5
                    },
                    sideIcons:''
                },
                {
                    id: 3,
                    src: require('../assets/modelThree.jpg'),
                    icons: ['fas fa-heart', 'fas fa-comment-alt', 'fas fa-share'],
                    position: {
                        y: 162,
                        x: 396,
                        z:5
                    },
                    sideIcons:''
                },
                {
                    id: 4,
                    src: require('../assets/modelFour.jpg'),
                    icons: ["fas fa-grin-hearts"],
                    position: {
                        y: 111,
                        x: 921,
                        z:5
                    },
                    sideIcons:''
                }
            ]
            }
        },

        mounted() {
           this.models.forEach(model => {
               model.sideIcons=this.getRandomSide()
           });
        },

        computed: {
            
        },

        methods: {
            getRandomSide: function () {
                var resp = Math.round(Math.random());
                var position = '';
                if (resp < 0.5) {
                    position = 'justify-start'
                } else {
                    position = 'justify-end'
                }
                return position;
            },
            
        }
    }
</script>